import React from 'react'
import './index.scss'

const Privacidad = ()=>{
    return <>
        <div id='privacidad'>
            <div className='privacidad__container'>
            <h1>Política de Privacidad KYC / AML Bitkkahpay</h1>
            <p>Lea atentamente el contenido completo de esta Política de privacidad. Al visitar Bitkkahpay, independientemente de si se registra o inicia sesión en los Servicios de Bitkkahpay, usted reconoce, comprende y da su consentimiento a todos los artículos descritos en esta Política de privacidad. No usaremos su información personal para ningún propósito que no esté cubierto en esta Política de privacidad.</p>
            <p>La Política de privacidad más reciente ha incorporado elementos del Reglamento general de protección de datos (GDPR) ya que actuamos de acuerdo con sus reglas de procesamiento de información personal. </p>
            <br/>
            <p><strong>1. INFORMACIÓN QUE RECOPILAMOS</strong></p>
            <p>Los siguientes son los tipos de información que recopilamos cuando se registra y utiliza los Servicios de Bitkkahpay:</p>
            <p><strong>1.1 Información que nos proporciona al registrarse</strong></p>
            <p>Cuando crea una cuenta de Bitkkahpay, nos proporciona su dirección de correo electrónico, nombre, fecha de nacimiento, nacionalidad, sexo, firma, facturas de servicios públicos, domicilio, contraseña y otra información para ayudarnos a identificarlo ("Información de identificación"). También puede optar por agregar un número de teléfono para SMS o cuenta de Google Authenticator para autenticación de dos factores para mejorar la seguridad de la cuenta.</p>
            <p><strong>1.2 Información que recopilamos para cumplir con las políticas regulatorias</strong></p>
            <p>Para cumplir con los estándares regulatorios de la industria global, los estándares regulatorios de la industria local y las órdenes gubernamentales en diferentes aspectos como Anti-Money Laundering (AML), Know-Your-Client (KYC) y Counter-Terrorist Financing (CTF), se requiere Bitkkahpay para recopilar información personal como documentos de identidad (incluido pasaporte, licencia de conducir, tarjeta de identidad nacional, tarjeta de identificación estatal, número de identificación fiscal, número de pasaporte, detalles de la licencia de conducir, etc.), comprobante de domicilio, fuente de la declaración del fondo, propósitos de los documentos del fondo y fuente de la riqueza ("Información de regulación"). Le explicaremos el contenido y los requisitos de dicha información personal cada vez que recopilemos información y nos reservamos el derecho de cambiar el contenido y los requisitos de la información recopilada a medida que cambien los estándares regulatorios de la industria global, los estándares regulatorios locales o las órdenes gubernamentales. Esta disposición se aplica a las cuentas de Bitkkahpay tanto personales como comerciales.</p>
            <p><strong>1.3 Información que recopilamos mientras utiliza los servicios de Bitkkahpay</strong></p>
            <p><strong>a. Información de uso del servicio</strong></p>
            <p>A través de su acceso y uso de los Servicios de Bitkkahpay, podemos monitorear y recopilar información de seguimiento relacionada con el uso, que incluye, entre otros, su número de teléfono, fecha y hora de acceso, tipo de dispositivo e identificación de dispositivo, sistema operativo y configuración de hardware, tipo de navegador e información derivada de la tarjeta SIM, operador de red, dirección IP, GPS, estación base y WLAN ("Información de uso del servicio"). Esta información puede ser obtenida directamente por Bitkkahpay o por medio de proveedores de servicios externos. La recopilación de información de uso del servicio ayuda a nuestros sistemas a garantizar que nuestra interfaz sea accesible para los usuarios en todas las plataformas y pueda ayudar durante las investigaciones penales.</p>
            <p><strong>b. Información de la transacción</strong></p>
            <p>Para todas las cuentas de usuario personales y comerciales, podemos recopilar información de transacciones a medida que usa los Servicios de Bitkkahpay, que incluyen, entre otros, instantáneas de depósitos, saldos de cuentas, historial comercial (como inicio de transacción, método de pago, precio, cantidad, tiempo, retiro y autorización). información), actividad de pedidos e historial de distribución ("Información de transacciones"). Recopilamos dicha información de transacciones para monitorear la actividad comercial sospechosa para la protección del usuario contra el fraude, la resolución de casos legales y cualquier otro propósito revelado en esta Política de privacidad.</p>
            <p><strong>c. Información de comunicación</strong></p>
            <p>Usted acepta que, para los fines divulgados en esta Política de privacidad, tenemos derecho a recopilar y utilizar la información contenida en o relacionada con la comunicación que nos envía o que genera a través de su uso de los Servicios de Bitkkahpay ("Información de comunicación"), que incluye:
            todos los mensajes, solicitudes u otra información que envíe en el curso de su contacto con Bitkkahpay;
            todas las comunicaciones y archivos adjuntos en relación con sus transacciones con otros usuarios u otros datos generados principalmente a través de sus comunicaciones con ellos.</p>
            <p><strong>d. Información financiera:</strong></p>
            <p>Usted acepta que, para los fines divulgados en esta Política de privacidad, tenemos derecho a recopilar y utilizar la información contenida en su información financiera o relacionada con ella cuando utiliza los Servicios de Bitkkahpay, incluidos, entre otros, la información de la cuenta bancaria, el número de cuenta principal de la tarjeta de pago (PAN), historial de transacciones, datos comerciales y / o identificación fiscal. Recopilamos dicha información financiera para monitorear la actividad financiera sospechosa para la protección del usuario contra el fraude, la resolución de casos legales y cualquier otro propósito revelado en esta Política de privacidad.</p>
            <br/>
            <p><strong>2. CÓMO RECOPILAMOS LA INFORMACIÓN</strong></p>
            <p><strong>2.1 Información que Bitkkahpay recopila automáticamente</strong></p>
            <p>La mayor parte de la información personal que recopilamos es proporcionada directamente por usted. En las siguientes situaciones, recopilaremos y procesaremos su información:</p>
            <p>a. Cuando se registra, inicia sesión o visita Bitkkahpay, o utiliza cualquier servicio de Bitkkahpay.</p>
            <p>b. cuando utiliza cookies del navegador o software al visitar o utilizar nuestro sitio web o aplicaciones.</p>
            <p><strong>2.2 Información recopilada de fuentes de terceros</strong></p>
            <p>Podemos recopilar información sobre usted de fuentes de terceros, incluidos, entre otros, los siguientes canales:</p>
            <p>a. bases de datos públicas, agencias de crédito y socios de verificación de identidad.</p>
            <p>b. socios de marketing y revendedores.</p>
            <p><strong>2.3 Datos agregados y anonimizados</strong></p>
            <p>La anonimización es una técnica de procesamiento de datos que elimina o modifica información personal para que no se pueda asociar con un individuo específico. A excepción de esta sección, ninguna de las otras disposiciones de esta Política de privacidad se aplica a los datos personales anonimizados o agregados (es decir, información sobre nuestros Usuarios que combinamos para que ya no identifique o haga referencia a un Usuario individual).</p>
            <p>Podemos utilizar datos de clientes anonimizados o agregados para cualquier propósito comercial, incluso para comprender mejor las necesidades y comportamientos de los Usuarios, mejorar nuestros productos y servicios, realizar inteligencia comercial y marketing, y detectar amenazas de seguridad. Podemos realizar nuestros propios análisis de datos anónimos o habilitar análisis proporcionados por terceros.</p>
            <br/>
            <p><strong>3. CÓMO UTILIZAMOS LA INFORMACIÓN QUE RECOPILAMOS</strong></p>
            <p>Usamos la información que recopilamos sobre usted para los siguientes propósitos o de las siguientes maneras:</p>
            <p><strong>3.1 Para proporcionar y mantener los servicios de Bitkkahpay</strong></p>
            <p>Usamos la información recopilada para entregar, mantener y mejorar los Servicios de Bitkkahpay (incluido, entre otros, el procesamiento de transacciones) y verificar las identidades de los Usuarios.</p>
            <p>Usamos la dirección IP y los identificadores únicos almacenados en las cookies de su dispositivo para ayudarnos a autenticar su identidad y actividades y proporcionar los Servicios Bitkkahpay. Dadas nuestras obligaciones legales y los requisitos del sistema, no podemos proporcionarle todos o algunos de los Servicios de Bitkkahpay sin datos como Información de identificación, Información de supervisión, Información de uso del servicio, Información de comunicación e Información de transacción.</p>
            <p><strong>3.2 Para proteger a nuestros usuarios</strong></p>
            <p>Usamos la información recopilada para proteger nuestras plataformas, las cuentas de los usuarios y los archivos.</p>
            <p>Usamos direcciones IP y cookies para protegernos contra abusos automatizados como spam, phishing y ataques de denegación de servicio distribuido (DDoS).</p>
            <p>Analizamos las actividades comerciales con el objetivo de detectar comportamientos sospechosos lo antes posible para evitar posibles fraudes y pérdidas de fondos a manos de los malos actores.</p>
            <p><strong>3.3 Para cumplir con los requisitos legales y reglamentarios</strong></p>
            <p>Con respecto a la privacidad y seguridad de la información personal, usaremos la información de conformidad con nuestras obligaciones legales, solicitudes gubernamentales y consultas razonables generadas por los usuarios. En los casos en que sea estrictamente necesario, como para proteger los intereses vitales de los Usuarios u otras personas físicas, para cumplir con el propósito de interés público, para perseguir nuestros intereses razonables (pero no para dañar los intereses de los Usuarios), podemos procesar su información personal sin su consentimiento. Excepto en las situaciones indicadas en esta Política de privacidad o los TOU, no divulgaremos ni proporcionaremos su información personal a terceros sin una revisión de nuestro equipo legal y / o el consentimiento previo del Usuario.</p>
            <p><strong>3.4 Para fines de medición, investigación y desarrollo</strong></p>
            <p>Medimos y analizamos activamente los datos para comprender la forma en que usa e interactúa con los Servicios de Bitkkahpay. Nuestros equipos de operaciones llevan a cabo esta actividad de revisión para mejorar continuamente el rendimiento de nuestras Plataformas y para resolver problemas con la experiencia del usuario. Además, utilizamos dicha información para personalizar, medir y mejorar los Servicios de Bitkkahpay y el contenido y el diseño de nuestros sitios web y aplicaciones, y para desarrollar nuevos servicios.</p>
            <p>Monitoreamos continuamente la información de actividad dentro de nuestros sistemas y nuestras comunicaciones con los usuarios para buscar y solucionar problemas rápidamente.</p>
            <p><strong>3.5 Para comunicarnos contigo</strong></p>
            <p>Utilizamos la información personal recopilada, como su número de teléfono o dirección de correo electrónico, para interactuar con usted directamente al brindar asistencia al cliente en un ticket o para mantenerlo informado sobre inicios de sesión, transacciones, seguridad de la cuenta y otros aspectos. Sin recopilar y procesar su información personal para confirmar cada comunicación, no podremos responder a sus solicitudes, preguntas y consultas enviadas. Todas las comunicaciones directas se guardan adecuadamente en Bitkkahpay o en el proveedor de servicios designado por Bitkkahpay, para que sean revisadas para verificar su precisión, para que se mantengan como evidencia o para que se utilicen para cumplir con otras obligaciones legales o contractuales.</p>
            <p><strong>3.6 Para hacer cumplir nuestras TOU y otros acuerdos</strong></p>
            <p>La información recopilada también se utiliza para hacer cumplir de manera continua y activa nuestros TOU y otros acuerdos con nuestros Usuarios, que incluyen, entre otros, revisar, investigar y prevenir cualquier actividad potencialmente prohibida o ilegal que pueda violar las disposiciones anteriores, o revelar la información relevante un tercero de acuerdo con el mismo.</p>
            <p>Bitkkahpay se reserva el derecho de suspender o terminar la prestación de cualquier Servicio de Bitkkahpay a cualquier Usuario que se encuentre involucrado en actividades que violen nuestras TOU y otros acuerdos con nuestros Usuarios.</p>
            <p><strong>3.7 Para facilitar adquisiciones, fusiones o transacciones corporativas</strong></p>
            <p>Podemos procesar cualquier información relacionada con su cuenta de Bitkkahpay y el uso de los servicios de Bitkkahpay según sea necesario en el contexto de adquisiciones corporativas, fusiones u otras transacciones corporativas.</p>
            <p><strong>3.8 Para marketing y publicidad</strong></p>
            <p>Podemos compartir su información personal con nuestros socios de marketing con fines de orientación, modelado y / o análisis, así como marketing y publicidad.</p>
            <p><strong>3.9 Para cualquier otro propósito</strong></p>
            <p>Podemos divulgar su información personal para cualquier otro propósito que usted consienta.</p>
            <br/>
            <p><strong>4. MARKETING</strong></p>
            <p>Tiene derecho a solicitarnos en cualquier momento que dejemos de comunicarnos con usted con fines de marketing o para enviarle información relevante. Si no desea ser contactado con fines de marketing en el futuro, haga clic en el enlace para cancelar la suscripción que se encuentra en la parte inferior del correo electrónico de marketing y envíe su solicitud para cancelar la suscripción.</p>
            <br/>
            <p><strong>5. CÓMO ALMACENAMOS Y PROTEGEMOS LOS DATOS DEL USUARIO</strong></p>
            <p>Bitkkahpay ha mantenido su información personal segura utilizando un almacenamiento seguro tolerante a fallas con encriptación estándar de la industria e implementó una serie de medidas de seguridad para garantizar que su información no se pierda, se abuse o altere, que incluyen, entre otras:</p>
            <p><strong>5.1 Medidas físicas</strong></p>
            <p>Los materiales que contienen su información personal se almacenarán en un lugar cerrado.</p>
            <p><strong>5.2 Medidas electrónicas</strong></p>
            <p>Los datos informáticos que contienen su información personal se almacenarán en los sistemas informáticos y medios de almacenamiento que están sujetos a estrictas restricciones de inicio de sesión.</p>
            <p><strong>5.3 Medidas de gestión</strong></p>
            <p>Solo los empleados autorizados pueden entrar en contacto con su información personal y dichos empleados deben cumplir con nuestras reglas internas de confidencialidad para los datos personales. También hemos impuesto estrictos controles de acceso físico a edificios y archivos.</p>
            <p><strong>5.4 Medidas técnicas</strong></p>
            <p>La tecnología de cifrado como PCI Scanning y Secured Sockets Layered Encryption se puede utilizar para transmitir y almacenar su información personal. Utilizamos varias tecnologías de seguridad general y sistemas de gestión de apoyo disponibles actualmente para minimizar los riesgos de que su información pueda ser divulgada, dañada, mal utilizada, accedida sin autorización, divulgada sin autorización o alterada.</p>
            <p><strong>5.5 Otras medidas</strong></p>
            <p>Nuestro servidor web está protegido por el "firewall".</p>
            <p>Tenga en cuenta que es imposible garantizar el 100% de seguridad de la información. Como tal, le solicitamos que comprenda la responsabilidad de tomar precauciones de seguridad de forma independiente para proteger su propia información personal. Usted acepta que no seremos responsables de ninguna fuga de información y otras pérdidas que no sean causadas por nuestra intención o negligencia grave, incluidas, entre otras, limitado a ataques de piratas informáticos, interrupciones de energía o fallas técnicas inevitables, en la medida máxima permitida por la ley.</p>
            <p>Si sospecha que su información personal se ha visto comprometida, especialmente la información de su cuenta y / o contraseña, bloquee su cuenta de Bitkkahpay y comuníquese de inmediato con el equipo de atención al cliente de Bitkkahpay en https://Bitkkahpay.com/contacto</p>   
            <p>A menos que la ley o los TOU estipulen lo contrario, usted acepta que tenemos el derecho, pero no la obligación, de retener toda la información personal recopilada durante la duración de su cuenta de Bitkkahpay durante 5 años más después de cerrar la cuenta.</p>
            <br/>
            <p><strong>6. TRANSFERENCIAS DE INFORMACIÓN PERSONAL</strong></p>
            <p>Usted comprende que tenemos derecho a que toda o parte de la información personal recopilada se transfiera o almacene en otros países o regiones que no sean su país de nacionalidad, su país de residencia o el país donde se encuentra el servidor, sin su consentimiento específico. en las siguientes circunstancias:</p>
            <p>6.1 si es necesario proteger, procesar, analizar, usar razonablemente la información personal.</p>
            <p>6.2 si es necesario para hacer cumplir los TOU y otros acuerdos con nuestros Usuarios.</p>
            <p>6.3 si es necesario para el interés público.</p>
            <p>6.4 si es necesario para establecer, ejercer o defender nuestros derechos, nuestros socios u otros Usuarios.</p>
            <p>6.5 otras circunstancias requeridas por la ley o las órdenes gubernamentales.</p>
            <br/>
            <p><strong>7. COOKIES</strong></p>
            <p><strong>7.1 ¿Qué son las cookies?</strong></p>
            <p>Las cookies son pequeñas cantidades de datos que se envían a su navegador y se almacenan en el disco duro de su computadora para recopilar información estándar de registro de Internet e información sobre el comportamiento de los visitantes. Cuando visita Bitkkahpay, podemos recopilar información sobre usted automáticamente a través de cookies o tecnologías similares.</p>
            <p>Para obtener más información, visite: www.allaboutcookies.org</p>
            <p><strong>7.2 ¿Cómo utilizamos las cookies?</strong></p>
            <p>Usamos cookies de varias formas para hacer que su experiencia en nuestras Plataformas sea más agradable, que incluyen, entre otras:</p>
            <p>a. Para mantenerlo registrado.</p>
            <p>b. Para registrar sus hábitos y preferencias al navegar por Bitkkahpay.</p>
            <p>c. Para registrar nuestro desempeño y verificar la efectividad de la publicidad en línea a través de Google Stats con cookies.</p>
            <p><strong>7.3 ¿Qué tipos de cookies utilizamos?</strong></p>
            <p>Usamos diferentes tipos de cookies, que incluyen, pero no se limitan a:</p>
            <p>a. Cookies funcionales</p>
            <p>Usamos dichas cookies para ayudarnos a identificarlo y recordar sus preferencias y configuraciones anteriores, que pueden incluir su dispositivo, su sistema operativo, su idioma preferido, su ubicación y otros datos de sesión. Utilizamos una combinación de cookies propias y de terceros.</p>
            <p><strong>7.4 ¿Cómo administrar las cookies?</strong></p>
            <p>La mayoría de los navegadores están preestablecidos para aceptar cookies. Puede ajustar la configuración de su navegador para evitar cookies o para notificarle tan pronto como se carguen las cookies. Sin embargo, detener todas las cookies puede significar que no puede acceder o utilizar algunas funciones de Bitkkahpay.</p>
            <br/>
            <p><strong>8. DERECHOS DE PROTECCIÓN DE DATOS DE LOS QUE DISFRUTA</strong></p>
            <p>Esperamos asegurarnos de que conoce plenamente los derechos de protección de datos de los que disfruta. A menos que la ley o las órdenes gubernamentales exijan lo contrario, cada Usuario tiene los siguientes derechos:</p>
            <p><strong>8.1 Derecho de acceso</strong></p>
            <p>Tiene derecho a acceder u obtener copias de su información personal. Podemos cobrar una tarifa razonable por proporcionar el servicio de acceso.</p>
            <p><strong>8.2 Derecho de rectificación.</strong></p>
            <p>Tiene derecho a corregir su información personal que considere inexacta. También tiene derecho a solicitarnos que completemos la información personal que considere incompleta dentro de un límite razonable.</p>
            <p><strong>8.3 Derecho al borrado (derecho al olvido)</strong></p>
            <p>Tiene derecho a solicitar la eliminación de sus datos personales en determinadas circunstancias.</p>
            <p><strong>8.4 Derecho a la restricción del procesamiento</strong></p>
            <p>En determinadas circunstancias, según lo requiera la ley o según lo recordemos específicamente, usted tiene derecho a solicitarnos que restrinjamos el procesamiento de su información personal; sin embargo, comprende que dicha restricción del procesamiento puede impedirnos brindarle algunos de los Servicios de Bitkkahpay.</p>
            <p><strong>8.5 Derecho a oponerse</strong></p>
            <p>En determinadas circunstancias, según lo requiera la ley o según lo recordemos específicamente, usted tiene derecho a oponerse al procesamiento de su información personal; sin embargo, usted comprende que tal objeción al procesamiento puede impedirnos brindarle algunos de los Servicios de Bitkkahpay.</p>
            <p><strong>8.6 Derecho a la portabilidad de datos</strong></p>
            <p>En determinadas circunstancias, según lo requiera la ley o según lo recordemos específicamente, usted tiene derecho a solicitarnos que transmitamos la información personal que recopilamos a otro organismo, o directamente a usted. Podemos cobrar una tarifa razonable por proporcionar el servicio de transmisión.</p>
            <br/>
            <p><strong>9. POLÍTICAS DE PRIVACIDAD DE OTROS SITIOS WEB</strong></p>
            <p><strong>9.1 Políticas de privacidad de sitios web que no pertenecen a Bitkkahpay.</strong></p>
            <p>Si visita otras plataformas de terceros o sitios web de socios, aplicaciones a través de los enlaces en Bitkkahpay, debe aceptar y cumplir con sus políticas de privacidad separadas e independientes. No somos responsables por el contenido o las actividades en el mismo.</p>
            <p><strong>9.2 Políticas de privacidad de Bitkkahpay</strong></p>
            <p>Esta Política de privacidad se aplica a todas las plataformas, sitios web y departamentos de Bitkkahpay.com y el ecosistema de Bitkkahpay. Si visita cualquier enlace a cualquier incluyen, de Bitkkahpay, debe aceptar y cumplir con las políticas de privacidad separadas e independientes de dichas incluyen, Si la política de privacidad de cualquier sub-plataforma entra en conflicto con esta Política de privacidad, prevalecerá la política de privacidad de dicha sub-plataforma.</p>
            <br/>
            <p><strong>10. CAMBIOS A ESTA POLÍTICA DE PRIVACIDAD</strong></p>
            <p>De vez en cuando, Bitkkahpay puede revisar esta Política de privacidad para reflejar cambios en la ley o en nuestras prácticas de recopilación, procesamiento y uso de datos personales. Los cambios a esta Política de privacidad se anunciarán mediante la publicación en https://www.Bitkkahpay.com/en/privacy. Deberá revisar periódicamente la Política de privacidad y prestar atención a sus revisiones. Si no está de acuerdo con el contenido revisado, deberá dejar de acceder a Bitkkahpay inmediatamente. </p>
            <br/>
            <p><strong>11. CONTÁCTENOS</strong></p>
            <p>Estamos comprometidos a respetar las libertades y los derechos de todos nuestros Usuarios de Bitkkahpay. Si tiene alguna pregunta o inquietud con respecto a esta Política de privacidad, o si desea presentar una solicitud de protección de datos, y comuníquese con nosotros en nuestra página de soporte en https://www.Bitkkahpay.com/contacto</p>
            <br/>
            <br/>
            <p>Cordialmente,</p>
            <p>Grupo Bitkkahpay</p>
            </div>
        </div>
    
    </>
}


export default Privacidad   